import { Injectable } from '@angular/core';
import { DatePipe } from '@angular/common';

@Injectable({
    providedIn: 'root'
})
export class DateService {
    constructor(
        private datepipe: DatePipe,
        ) { }

    setDate(value): Date {
        if (!value) return null;

        return new Date(this.datepipe.transform(value, 'yyyy-MM-dd'));
    }

}