import { NgModule } from '@angular/core';
import { URL_PATHS } from '@shared';
import { RouterModule, Routes } from '@angular/router';



const routes: Routes = [

  {
    path: '',
    children: [
      {
        path: URL_PATHS.USER_ROLE,
        loadChildren: () => import('./user-role/user-role.module')
          .then(m => m.UserRoleModule)
      },
      {
        path: URL_PATHS.USER_REGISTRATION,
        loadChildren: () => import('./user-registration/user-registration.module')
          .then(m => m.UserRegistrationModule)
      },
      {
        path: URL_PATHS.UPDATE_USER + '/:id',
        loadChildren: () => import('./user-registration/user-registration.module')
          .then(m => m.UserRegistrationModule)
      },
      {
        path: URL_PATHS.USER_PROFILE,
        loadChildren: () => import('./user-profile/user-profile.module')
          .then(m => m.UserProfileModule)
      },
      {
        path: URL_PATHS.USER_LIST,
        loadChildren: () => import('./user-list/user-list.module')
          .then(m => m.UserListModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AccountRoutingModule { }
